$brk-base-colors: ();
$brk-base-colors: map-merge((
				"brand-primary":      darken(adjust-hue(#2775FF, -39), 13.73),
				"secondary":          lighten(darken(adjust-hue(#7202bb, -109), 2.12), 6.08),
				"white":              #fff,
				"black":              #000,
				"brk-dark-base":      #272727,
				"brk-base-1":         darken(adjust-hue(#69adff, -39), 13.73),
				"brk-base-2":         darken(adjust-hue(#00c6ff, -39), 13.73),
				"brk-base-3":         darken(adjust-hue(#0F5AE0, -39), 13.73),
				"brk-base-4":         #e8ecff,
				"brk-base-5":         darken(adjust-hue(#00f6ff, -39), 13.73),
				"brk-base-6":         darken(adjust-hue(#0B98F6, -39), 13.73),
				"brk-base-7":         darken(adjust-hue(#a7ebff, -39), 13.73),
				"brk-base-8":         darken(adjust-hue(#1600ba, -39), 13.73),// TODO: !!!
				"brk-secondary-1":    lighten(darken(adjust-hue(#4f34c9, -109), 2.12), 6.08),
				"brk-secondary-2":    lighten(darken(adjust-hue(#4F00BA, -109), 2.12), 6.08),
				"brk-secondary-3":    lighten(darken(adjust-hue(#7400ba, -109), 2.12), 6.08),
				"brk-secondary-4":    lighten(darken(adjust-hue(#6e50c2, -109), 2.12), 6.08),
				"brk-secondary-5":    lighten(darken(adjust-hue(#6377DF, -109), 2.12), 6.08),
				"brk-secondary-6":    lighten(darken(adjust-hue(#b50aff, -109), 2.12), 6.08),
				"text-base-color-1":  #c4c4c4,
				"text-base-color-2":  darken(adjust-hue(#0F5AE0, -39), 13.73),
				"text-base-color-3":  #f3f3f3,
				"form-color-1":				#e8ecff
), $brk-base-colors);

@function safe-rgb($color) {
	@return unquote("#{red($color)}, #{green($color)}, #{blue($color)}");
}

/* ----------- Palette variable ----------- */
:root {
	@each $color, $value in $brk-base-colors {
		--#{$color}: #{$value};
	}

	// RGB
	@each $color, $value in $brk-base-colors {
		--#{$color}-rgb: #{safe-rgb($value)};
	}

	--b-radius: inherit;
}
/* ----------- Palette variable ----------- */
@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.brk-portfolio-card {
  position: relative;
  display: block;
  flex: none;
  width: 100%;
  height: 380px;
  color: $white;
  perspective: 1000px;
  &__figure,
  &__image {
    display: block;
    width: 100%;
    height: 100%;
    margin: 0;
  }
  &__image {
    object-fit: cover;
    object-position: center;
  }
  &__figure > * {
    transform: translateZ(0px);
  }
  .brk-tilter__figure {
    position: relative;
  }
  &__deco {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  &__caption {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    padding: 42px 34px 42px 34px;
  }
  &__category {
    color: var(--white);
    border-radius: 20px;
    text-transform: uppercase;
    margin-left: 1px;
    padding: 5px 18px;
    opacity: 0;
    transition: opacity 0.3s ease;
    box-shadow: 0 5px 16px rgba(var(--brand-primary-rgb), 0.5);
    background-color: var(--brand-primary);
  }
  &__title {
    margin-top: 20px;
    margin-left: 4px;
    color: $white;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  &__link {
    text-decoration: underline;
    text-transform: uppercase;
    color: $white;
    position: absolute;
    left: 41px;
    bottom: 41px;
    cursor: pointer;
    z-index: 4;
    opacity: 0;
    transition: opacity 0.3s ease;
  }

  &__btn {
    &-reverse {
      color: var(--brand-primary) !important;
      background: var(--white);

      .before {
        background-color: var(--white);
      }
      .after {
        background-color: var(--white);
      }
    }
    &__icon {
      &::before {
        color: var(--brand-primary);
      }
    }
  }

  &__btn-reverse {
    position: absolute !important;
    right: 28px;
    bottom: 23px;
    font-weight: 100;
    z-index: 4;
    cursor: pointer;
    opacity: 0 !important;
    transition: opacity 0.3s ease;
    &__icon {
      font-size: 18px;
    
    }
  }
  &:hover {
    .brk-portfolio-card {
      &__deco,
      &__category,
      &__title,
      &__link,
      &__btn-reverse {
        opacity: 1 !important;
      }
      &__link {
        color: $white;
      }
    }
  }
}
